.cssbuttons-io {
  position: relative;
  font-family: inherit;
  font-weight: bold;
  font-size: 16px;
  letter-spacing: 0.1rem;
  border-radius: 0.8em;
  border-color: rgb(124 58 237);
  border-width: 2px;
  background: linear-gradient(to right, #8e2de2, #4a00e0);
  color: ghostwhite;
  overflow: hidden;
}

.cssbuttons-io svg {
  width: 1em;
  height: 1em;
  margin-right: 0.5em;
}

.cssbuttons-io span {
  position: relative;
  z-index: 10;
  transition: color 0.4s;
  display: inline-flex;
  align-items: center;
  padding: 0.2em 1em 0.2em 1em;
}

.cssbuttons-io::before,
.cssbuttons-io::after {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 0;
}

.cssbuttons-io::before {
  content: "";
  background: #000;
  width: 120%;
  left: -10%;
  transform: skew(30deg);
  transition: transform 0.4s cubic-bezier(0.3, 1, 0.8, 1);
}

.cssbuttons-io:hover::before {
  transform: translate3d(100%, 0, 0);
}

.cssbuttons-io:active {
  transform: scale(0.95);
}

@media screen and (max-width: 1055px) {
  .cssbuttons-io span {
    padding: 0em 0.5em 0em 0.5em;
  }
} ;
